import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import ChildSecCard from "./ChildSecCard";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    background:"linear-gradient(90deg, #fc5c7d 0%,#6a82fb 100% )",    
    borderRadius:"5px"
  },
  paper: {
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

export default function ChildSec() {
  const classes = useStyles();

  return (
    <div className="Child-sec" >
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item lg={3} md={6} sm={6} xs={6} >
          <Paper className={classes.paper}
            style={{
              background: "none",
              boxShadow: "none"   ,       
            }}>
                <ChildSecCard ChildSecIcon="fas fa-building" ChildSecText="IT CONSULTING"/>
            </Paper>
        </Grid>
        <Grid item lg={3} md={6} sm={6} xs={6} style={{}}>
          <Paper className={classes.paper}
            style={{
              background: "none",
              boxShadow: "none",
            }}>
                <ChildSecCard ChildSecIcon="fas fa-headset" ChildSecText="24/7 Support"/>
            </Paper>
        </Grid>
        <Grid item lg={3} md={6} sm={6} xs={6}>
          <Paper className={classes.paper}
            style={{
              background: "none",
              boxShadow: "none",
            }}>
                <ChildSecCard ChildSecIcon="fas fa-rupee-sign" ChildSecText="AFFORDABLE PRICE"/>
            </Paper>
        </Grid>
        <Grid item lg={3} md={6} sm={6} xs={6}>
          <Paper className={classes.paper}
            style={{
              background: "none",
              boxShadow: "none"
            }}>
                <ChildSecCard ChildSecIcon="fas fa-shield-alt" ChildSecText="MORE SECURE"/>
            </Paper>
        </Grid>
      </Grid>
    </div>
    </div>
  );
}
