import JobReq from "./JobReq";

const HeadColor = {
    color: "#eee"
}
const JobDescText = {
    lineHeight: "1.5",
    color: "#bcbcbc",
    textAlign: "justify",
    fontSize: "1.2rem",
    marginTop: "35px"
}
const JobDesc = () => {
    return ( 
        <div style={{backgroundColor: "#121212",padding: "5% 20%"}}className="JobDescSec">

            <h2 style={HeadColor}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Overview</span></h2>
            <p style={JobDescText}>Were looking for a talented and passionate Frontend Engineer to join our team. Were an agile and fast-paced team with complimenting skill-sets and our products have so far reached people in over 128 countries with three million users. We iterate and launch fast, build beautiful web apps with solid technical foundations, and work hard with an insatiable hunger for success. Every day, we walk in to unravel complex problems and end the day delightfully. Despite the rapid pace, we have a relaxed office environment.</p>

            <div style={{marginTop: "10%",textAlign: "justify"}}>
                <h2 style={HeadColor}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Qualifications:</span></h2>

                <ul style={{marginTop: "40px",lineHeight:"1.5"}}>
                    <JobReq JobPoints="Partner with internal stakeholders to establish technical road maps that accomplish our business objectives."/>
                    <JobReq JobPoints="Understand complex, ambiguous, open-ended business problems and produce an actionable, results-oriented strategy to solve these challenges."/>
                    <JobReq JobPoints="Build and action product architectures and solution roadmaps, including the design of infrastructure necessary to support the business objectives of our stakeholders."/>
                    <JobReq JobPoints="Create launch programs with partners to ensure that new products and feature releases are tested before broad-scale deployment."/>
                    <JobReq JobPoints="Perform implementation reviews, advocate new product features and ensure the prompt and proper resolution of technical challenges."/>
                </ul>                
            </div>
           
            <div style={{marginTop: "10%",textAlign: "justify"}}>
                <h2 style={HeadColor}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Responsibilities:</span></h2>

                <ul style={{marginTop: "40px",lineHeight:"1.5"}}>
                    <JobReq JobPoints="Partner with internal stakeholders to establish technical road maps that accomplish our business objectives."/>
                    <JobReq JobPoints="Understand complex, ambiguous, open-ended business problems and produce an actionable, results-oriented strategy to solve these challenges."/>
                    <JobReq JobPoints="Build and action product architectures and solution roadmaps, including the design of infrastructure necessary to support the business objectives of our stakeholders."/>
                    <JobReq JobPoints="Create launch programs with partners to ensure that new products and feature releases are tested before broad-scale deployment."/>
                    <JobReq JobPoints="Perform implementation reviews, advocate new product features and ensure the prompt and proper resolution of technical challenges."/>
                </ul>

            </div>
            
            <div style={{background: "#5394f8",
                        color: "#eee",padding: "20px",borderRadius: "10px",fontSize: "1.2rem",marginTop: "50px"}}>
                    <p>Individuals seeking employment at DataRobot are considered without regards to race, color, religion, 
                        national origin, age, sex, marital status, ancestry, physical or mental disability, veteran status,
                         gender identity, or sexual orientation.</p>
            </div>
            <div style={{marginTop:"10%"}}>
            <h2 style={HeadColor}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Location:</span></h2>
                <iframe src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15546.324966621085!2d80.2743577!3d13.0623059!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xff085994ddd592d6!2sjuzlyktat!5e0!3m2!1sen!2sin!4v1613738157526!5m2!1sen!2sin"  frameborder="0" style={{border:"0",width:"100%",height:"500px",marginTop: "20px"}} allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
            </div>
        </div>
     );
}
 
export default JobDesc;