import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import JobIcon from "./JobIcon";
import {Link} from "react-router-dom";

const useStyles = makeStyles({
  root: {
    minWidth: 275,
    background: "none"
  },
  title: {
    fontSize: 14,
    color: "#eee",
    borderLeft: "3px solid #1167b2",
    paddingLeft: "10px",
    fontWeight: "bold",
    fontSize: "1.2rem"
  },
  pos: {
    color: "#bcbcbc",
    fontWeight: "bold",
    marginTop: "10px",
    marginLeft: "15px"
  },
});

export default function JobCard(props) {
  const classes = useStyles();

  return (
    <Card className="JobCard" variant="outlined"style={{backgroundColor: "#191a1c",transition: "0.5s"}}>
      <CardContent>
        <Typography className={classes.title} gutterBottom>
          {props.Jobtitle}         
          
          <JobIcon LangIcon={props.JobLangIcon1} IconColor={props.JobLangColor1} />
          <JobIcon LangIcon={props.JobLangIcon2} IconColor={props.JobLangColor2}/>
          <JobIcon LangIcon={props.JobLangIcon3} IconColor={props.JobLangColor3}/>

        </Typography>
        <Typography className={classes.pos} >
            Qualifications :
          <ul>
              <li style={{
                  fontSize: "12px"
              }}>{props.JobDesc1}</li>
              <li style={{
                  fontSize: "12px"
              }}>{props.JobDesc2}</li>
          </ul>
        </Typography>
        <Link to="/Job">
          <Button style={{
              color: "#1167b2",
              float: "right",
              fontWeight: "bold"
          }}>Expand<i className="fas fa-angle-double-right"style={{
              marginLeft: "5px"
          }}></i></Button>
        </Link>
      </CardContent>
    </Card>
  );
}
