import { makeStyles } from '@material-ui/core/styles';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Form, Container, Row, Col, Image } from 'react-bootstrap';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
      '& > *': {
        margin: theme.spacing(1),
      }
    }
  }));

const ApplyForm = () => {

    const classes = useStyles();

    return (
      <div className={classes.root}>
        <div className="p-3 ApplyFormSec" style={{backgroundColor: "#171717",margin: "50px 10%",boxShadow: "0px 0px 10px #000"}}>
            
            <h1 style={{textAlign: "center",fontSize: "3rem"}}className="ApplyFormHeadText"><span style={{color:"#eee",borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Apply for this job</span></h1>

           <Container>
                <Row>
                    <Col xs={12} lg={6}className="pt-5">
                            <Form.Group controlId="formGroupFirstName">
                                <Form.Control type="text" className="p-4" placeholder="First Name" />
                            </Form.Group>

                          </Col>
                          <Col xs={12} lg={6} className="pt-lg-5">
                          <Form.Group controlId="formGroupLastName">
                                <Form.Control type="text" className="p-4 ApplyFormLnameBox" placeholder="Last Name" />
                            </Form.Group>
                          </Col>
                </Row>

                <Row>
                    <Col xs={12} lg={6}className="">
                      <Form.Group controlId="formGroupEmail">
                                  <Form.Control type="email" className="mt-3 p-4" placeholder="Email" />
                        </Form.Group>

                      </Col>
                      <Col xs={12} lg={6} className="">
                            
                        <Form.Group controlId="formGroupPhone">
                            <Form.Control type="text" className="mt-3 p-4" placeholder="Mobile Number" />
                        </Form.Group>
                      </Col>
                </Row>


                  <Row>
                    <Col xs={12} lg={6}className="">
                    <Form.Group controlId="formGroupPhone">
                                <Form.Control type="text" className="mt-3 p-4" placeholder="Mobile Number" />
                            </Form.Group>

                          </Col>
                          <Col xs={12} lg={6} className="">

                          <label style={{color: "#eee"}}>Upload Resume <sup style={{color:"red"}}>*</sup></label><br />
                          
                          <input
                                accept="image/*"
                                className={classes.input}
                                id="contained-button-file"
                                multiple
                                type="file"
                                style={{display: "none"}}
                              />
                            <label htmlFor="contained-button-file">
                            <Button variant="contained" color="primary" component="span">
                              Upload
                            </Button>
                          </label>
                          </Col>
                    </Row>

                  <Row>
                    <Col  xs={12} className="">
                      <Form.Group controlId="exampleForm.ControlTextarea1">
                                  <Form.Control as="textarea" className="mt-3" rows={5} placeholder="Enter Message" />
                      </Form.Group>
                    </Col>
                  </Row>
                <div style={{textAlign: "center"}}>
                  <Button style={{backgroundColor:"#007FFF",color:"#eee",padding: "10px 50px"}} type="submit">Submit</Button>
                </div>                   
          
            </Container>

        </div>
        </div> 
     );
}
 
export default ApplyForm;