import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import {Link} from "react-router-dom";
import BlogLists from "./BlogLists";

const BlogSingleCard = () => {
    return ( 
        <div className="BlogSingleCard">
        <Grid container spacing={3}>
            {BlogLists.map(Blog => (

            <Grid item lg={6} md={6} sm={12} xs={12}>

            <Paper style={{
                background: "none",
                boxShadow: "none"
            }}>

            <img src={Blog.image} style={{width: "100%",height: "253px"}} alt="Blog-Image"/>
            <h6 style={{color: "#007FFF",marginTop: "10px"}}>{Blog.title}</h6>
            <p style={{color: "#eee"}}><span>{Blog.date}</span><span>{Blog.category}</span></p>
            <p style={{color: "#bcbcbc",marginTop: "-10px"}}>{Blog.desc} . . . <Link to="/Careers"> Read More </Link> </p>

            </Paper>

            </Grid>

       
            ))}


             </Grid>
        </div>
     );
}
 
export default BlogSingleCard;