import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import ServicesCard from "./ServicesCard";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function ServicesCardGrid() {
  const classes = useStyles();

  return (
    <div className="servicesCardGrid">
      <Grid container spacing={3}>
        <Grid item lg={6}md={6}sm={12}xs={12}>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"
            }}>
                <ServicesCard ServiceIcon="fas fa-laptop-code" ServiceTitle="WEB DEVELOPMENT" ServiceText1="Get a faster 
                website and convert more! Custom Website Development with great UI design.A website is a great way to 
                strengthen the personal references of other customers." 
                ServiceText2="It is important to ensure that your website is compatible across different browsers because 
                not all people use the same browser. The purpose of cross-browser testing is to provide a consistent 
                behavior and experience across all browsers, devices, and platforms." />
            </Paper>
        </Grid>
        <Grid item  lg={6}md={6}sm={12}xs={12}>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"
            }}>
                <ServicesCard ServiceIcon="fab fa-sketch" ServiceTitle="GRAPHIC DESIGNING" ServiceText1="Graphic design is 
                the craft of creating visual content to communicate messages. You can discover top graphic design ideas & 
                concepts created by our highly creative designerGraphic design is the craft of creating visual content to 
                communicate messages." 
                ServiceText2="Graphic design is a crucial tool that makes sure that you communicate with your people in an efficient manner. It serves to deliver your message to the target audience in an aesthetic.
                " />
            </Paper>
        </Grid>
        <Grid item  lg={6}md={6}sm={12}xs={12}>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"
            }}>
                <ServicesCard ServiceIcon="fas fa-file-code" ServiceTitle="
                SOFTWARE DEVELOPMENT" ServiceText1="Software development is a process by which standalone or individual 
                software is created using a specific programming language. Discover software development innovations and 
                technologies." 
                ServiceText2="Software development process is the process of dividing software development work into distinct phases to improve design, product management,and project management,product management,and project management.
                " />
            </Paper>
        </Grid>
        <Grid item  lg={6}md={6}sm={12}xs={12}>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"
            }}>
                <ServicesCard ServiceIcon="fas fa-ad" ServiceTitle="
                DIGITAL MARKETING" ServiceText1="Digital marketing is advertising delivered through digital channels. 
                Channels such as social media, mobile applications, email, web applications, search engines, websites, or 
                any new digital channel. Promotion of products, services or brands via digital marketing channels." 
                ServiceText2="Affiliate marketing is the process by which an affiliate earns a commission for marketing another person's or company's products.
                " />
            </Paper>
        </Grid>
        <Grid item  lg={6}md={6}sm={12}xs={12}>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"
            }}>
                <ServicesCard ServiceIcon="fab fa-sketch" ServiceTitle="
                E-COMMERCE" ServiceText1="E-commerce is the buying and selling of goods and services, or the 
                transmitting of funds or data, over an electronic network, primarily the internet. Modern 
                electronic commerce typically uses the World Wide Web for at least one part of the 
                transaction's life cycle although it may also use other technologies such as e-mail." 
                ServiceText2="Consumer-to-Business(C2B), is a business consumers create products and services which are consumed by businesses and organizations.
                " />
            </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
