import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import FooterLink from "./FooterLink";
import FooterSocialLink from "./FooterSocialLink";
import {Link} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
    textAlign: "center"
  },
}));

export default function MobileFooter() {
  const classes = useStyles();

  return (
    <div >
      <Accordion style={{backgroundColor: "#080808",color: "#eee"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          
        >
          <Typography className={classes.heading}>Services</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <Link to="/Services"style={{textDecoration: "none"}}>
              <FooterLink FooterLink="Web Designing & Development" />
              <FooterLink FooterLink="Software Development" />
              <FooterLink FooterLink="Graphic designing" />
              <FooterLink FooterLink="Digital Marketing" />
              <FooterLink FooterLink="E-Commerce Development" />
              <FooterLink FooterLink="Whatsapp Link Generator" />
              </Link>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion style={{backgroundColor: "#080808",color: "#eee"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography className={classes.heading}>Partners</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <FooterLink FooterLink="Reseller Program" />
            <FooterLink FooterLink="Affiliate Program" />
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion style={{backgroundColor: "#080808",color: "#eee"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography className={classes.heading}>Follow Us On</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <FooterSocialLink FooterSlink="fab fa-facebook" />
              <FooterSocialLink FooterSlink="fab fa-instagram" />
              <FooterSocialLink FooterSlink="fab fa-twitter" />
              <FooterSocialLink FooterSlink="fab fa-linkedin-in" />
              <FooterSocialLink FooterSlink="fab fa-quora" />
              <FooterSocialLink FooterSlink="fab fa-medium-m" />
              <FooterSocialLink FooterSlink="fab fa-pinterest" />
              <FooterSocialLink FooterSlink="fab fa-youtube" />
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
