import ProductTop from "../../Images/Products/ProductTop.jpg";

const ProductTopSec = () => {
    return ( 
        <div style={{backgroundColor: "#171717"}}className="ProductTopSec">
            <h1 style={{textAlign: "center",color: "#eee"}}className="ProductTopSecHeadText">We build awesome <span style={{color: "#007FFF"}}>digital products</span></h1>
            <p className="ProductTopSecHeadText2">Equipped with a handful of key services, we are primed to handle any problem thats thrown at us. And as much as we like hearing about our clients problems, we absolutely love it when our team solves them.</p>

            <div className="ProductTopImgSec">
                <img className="ProductTopImg" style={{width: "100%"}}  src={ProductTop} alt=""/>
            </div>
            

        </div>
     );
}
 
export default ProductTopSec;