import Button from '@material-ui/core/Button';


const ButtonStyle = {
    color: "#eee",
    backgroundColor: "#007FFF",
    padding: "10px 50px",
    fontWeight: "bold",
    float: "right",
    position: "position",
    top: "-10px"
}

const JobTopSec = () => {
    return ( 
        <div style={{padding: "8% 10% 3%"}} className="JobTopSec">
            
            <h1 style={{color: "#eee"}}className="JobTopSecHeadText">FRONT END DEVELOPER</h1>
            <p style={{color: "#bcbcbc",fontWeight: "500",}}className="JobTopSecHeadText2">
            <i className="fas fa-briefcase"style={{color: "#007FFF",marginRight: "10px"}}></i><span style={{paddingRight: "5%"}}>FULL TIME</span>
                <i className="fas fa-map-marker-alt"style={{color: "#007FFF",marginRight: "10px"}}></i><span>CHENNAI</span>
                <Button style={ButtonStyle}className="JobTopSecHeadBtn">Apply</Button>
            </p>
            
        </div>
     );
}
 
export default JobTopSec;