import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Services from "../../Images/Services/Services.png";

const useStyles = makeStyles((theme) => ({
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary
    },
  }));

const ServicesTopSec = () => {
    const classes = useStyles();

    return (                 
        
        <div style = {
            {                
                margin: "0 10%",
                boxShadow: "0px 2px 8px #000",
                borderRadius: "10px"
            }            
        } className="servicesTopMainSec">
           <div className="servicestopcol1">
                <Grid container spacing={3}>
                    <Grid item lg={8} md={6} xs={12} style={{background: "linear-gradient(92.88deg, rgb(69, 94, 181) 9.16%, rgb(86, 67, 204) 43.89%, rgb(103, 63, 215) 64.72%)"}}>
                        <Paper className={classes.paper}style={{
                            
                            background: "none",
                            boxShadow: "none"
                            }}>
                            
                            <h1 style={{
                                color: "#f8f8f8",
                                fontSize: "3.5rem",
                                paddingLeft: "4%"
                            }}className="serviceTopText">Our</h1>
                            <h1 style={{
                                color: "#f8f8f8",
                                fontSize: "3.5rem",
                                marginTop: "-20px",
                                paddingLeft: "4%"
                            }}className="serviceTopText2">Top Services</h1>

                            <p style={{
                                color: "#f8f8f8",
                                fontSize: "1.2rem",
                                paddingLeft: "4%",
                                lineHeight: "1.5"
                            }}className="serviceTopText3">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Maecenas maximus mi nec tellus hendrerit 
                                elementum. Pellentesque habitant morbi tristique senectus et netus etc </p>
                            <button className="LMore-services-btn">See More <i class="fas fa-angle-down LMore-services-icon"></i> </button>
                        </Paper>
                    </Grid>
                    <Grid className="servicestopimg" item lg={4} md={6} xs={12} style={{
                        backgroundColor: "#f6f6f6",
                    }}>
                    <Paper className={classes.paper}style={{
                            background: "none",
                            boxShadow: "none"
                            }}>

                              <img src={Services}  style={{                                  
                                      width: "100%",
                                      height: "100%"                                  
                                    }} alt=""/>  
                            </Paper>
                    </Grid>
                </Grid>
            </div>
        </div>
    );
}

export default ServicesTopSec;