import Navbar from "./Components/Navbar/Navbar";
import ChildSec from "./Components/Home/ChildSec";
import HomeCard from "./Components/Home/HomeCard";
import HomeServicesSec from "./Components/Home/HomeServicesSec";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Footer from "./Components/Footer/Footer";
import {Link} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing(2),
  }
}));

export default function Home() {
  const classes = useStyles();
  return (
    <div >
      <Navbar />
      <ChildSec />
      <HomeCard />
      <HomeServicesSec />
      <div
        style={{
          backgroundColor: "#121212",
          padding: "50px 0"
        }}
      >
        <h2
          style={{
            color: "#fff",
            textAlign: "center"
          }}
        >
          START YOUR <span style={{color: "#007FFF"}}> FREE CONSULTATION</span>
        </h2>

        <Grid
          container
          spacing={3}
          style={{
            padding: "0 5%"
          }}
        >
          <Grid item xs={12} sm={12} md={8} lg={8}>
            <Paper
              style={{
                background: "none",
                boxShadow: "none"
              }}
              className={classes.paper}
            >
              <p
                style={{
                  color: "#fff",
                  fontSize: "1.5rem",
                  textAlign: "justify"
                }}
              >
                We Don't Make Only Websites Or Applications, We Build Brands
                Want to know more about us or to know more about us, please
                click the button below.
              </p>
            </Paper>
          </Grid>
          <Grid item xs={12} sm={12} md={4} lg={4}>
            <Paper
              style={{
                background: "none",
                boxShadow: "none",
                textAlign: "center"
              }}
              className={classes.paper}
            >
              <Link to="/Contact" className="HomeCnct-btn">
                Contact Us
              </Link>
            </Paper>
          </Grid>
        </Grid>
      </div>
      <Footer />
    </div>
  );
}
