import {Carousel} from 'react-bootstrap';
import blog1 from "../../Images/Blogs/blog1.jpg";
import blog2 from "../../Images/Blogs/blog2.png";
import blog3 from "../../Images/Blogs/blog3.jpg";

const BlogCarousel = () => {
    return ( 
        <div className="BlogCarousel">
            <h2 style={{color: "#eee"}}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Best <span style={{color: "#007FFF"}}>blogs</span></span></h2>
            <Carousel className="mt-5">
                <Carousel.Item>
                    <img
                    className="d-block w-100 carousel-img"
                    src = {blog1}
                    alt="First slide"
                    />
                    <Carousel.Caption>
                        <h3 style={{color: "#eee"}}>FATHERHOOD</h3>
                        <p>The thoughts of child rearing a kid have advanced over the previous century and keep on.</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100 carousel-img"
                    src={blog2}
                    alt="Second slide"
                    />

                    <Carousel.Caption>
                        <h3>Sex Inequality in Education</h3>
                        <p>For a huge number of years, ladies have been deplored for battling their central right to</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100 carousel-img"
                    src={blog3}
                    alt="Third slide"
                    />

                    <Carousel.Caption>
                        <h3 style={{color: "#000"}}>Sex Inequality in Education</h3>
                        <p style={{color: "#000"}}>For a huge number of years, ladies have been deplored for battling their central right to.</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </div>
     );
}
 
export default BlogCarousel;