export default function FooterSocialLink(props) {
    return (
      <a
        href=""
        style={{
          textDecoration: "none",
          cursor: "pointer",
          transition: "0.5s",
          marginRight: "10px"
        }}
        className="footerSlink"
      >
        <i class={props.FooterSlink}></i>
      </a>
    );
  }
  