import ContactLayout from "./ContactLayout";
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      color: theme.palette.text.secondary,
      background: "none",
      boxShadow: "none"
    },
  }));

const ContactCard = () => {
    const classes = useStyles();    

    return (
        <div className="contact">
            <Grid container spacing={3}>
        <Grid item lg={3} md={6} sm={6} xs={12}>
          <Paper className={classes.paper}>
          <ContactLayout title="Call Us" text="+91 90909 12129" img="fas fa-phone-alt" />
          </Paper>
        </Grid>
        <Grid item  lg={3} md={3} sm={6} xs={12}>
          <Paper className={classes.paper}>
          <ContactLayout title="E-mail" text="info@juzlyktat.com" img="far fa-envelope" />
          </Paper>
        </Grid>
        <Grid item  lg={3} md={3} sm={6} xs={12}>
          <Paper className={classes.paper}>
          <ContactLayout title="Send Message" text="Send us a message" img="fab fa-whatsapp"/>
          </Paper>
        </Grid>
        <Grid item  lg={3} md={3} sm={6} xs={12}>
          <Paper className={classes.paper}>
          <ContactLayout title="Live Chat" text="Chat with us 24/7" img="far fa-comment-dots"/>
          </Paper>
        </Grid>
      </Grid>         
            
            
            
        </div>
    );
}

export default ContactCard;