import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import WhyWeText from "./WhyWeText";
import Career from "../../Images/Careers/Career.jpg";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,
  },
}));

export default function WhyWe() {
  const classes = useStyles();

  return (
    <div className={classes.root} style={{
        backgroundColor: "#030303",
        padding: "50px 10%"
    }}>
      <Grid container spacing={3}>
        <Grid item xs>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"            
            }}>

                <h1 style={{
                    color: "#ccc"
                }}>WHY WE</h1>
                 <h1 style={{
                    color: "#1167b2",
                    fontSize: "3rem",
                    marginTop: "-15px"
                }}>WORK HERE</h1>

                <img src={Career}style={{
                  width: "100%",
                  height: "100%"
                }} alt=""/>
            </Paper>
        </Grid>
        <Grid item xs>
          <Paper className={classes.paper} style={{
              background: "none",
              boxShadow: "none"
            }}>
                <WhyWeText WhyWeText1="Build together" WhyWeText2="We are respectful, sharing, teaching and learning. We 
                are smart, weird, fun and above all, we're a team and want to see each other succeed personally and 
                professionally."/>
                <WhyWeText WhyWeText1="Always transparent" WhyWeText2="We have nothing to hide from each other. We are 
                open, vulnerable, and straight to the point. Regardless of team or title, all knowledge and enthusiasm is 
                appreciated."/>
                <WhyWeText WhyWeText1="Adapting to grow" WhyWeText2="We adapt and grow with the way the world is changing.
                 We never get stuck in one place. Our standards are very high, and we work very hard to live up to them."/>
                 <WhyWeText WhyWeText1="Exceeding expectations" WhyWeText2="We want to exceed expectations. To give them 
                 more than they knew they wanted. Our standards are very high, and we work very hard to live up to them."/>
                 <WhyWeText WhyWeText1="Have fun" WhyWeText2="We dont just work all day. We love to unwind by playing 
                 games and enjoying each others company. We are here, were alive, and its pretty damn great."/>
            </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
