import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';

export default function OurProductCard(props) {

  return (
    <div className="OurProductCard">
      <Grid container spacing={3} style={{boxShadow: "0px 0px 10px #000"}}>
        <Grid item xs={12}>
          <Paper style={{
              background: "#121212",
              boxShadow: "none"
          }}>
              <img style={{width: "100%",height: "300px"}} src={props.ProductImg} alt="Product-JUZPAYRENT"/>
              <h2 style={{color: "#eee",marginTop: "10px"}}>{props.ProductTitle}</h2>
              <p style={{color: "#bcbcbc"}}>{props.ProductDesc}</p>
              <Button style={{backgroundColor: "#04509d",color: "#eee",padding: "8px 20px",fontWeight: "bold"}}>Click to see <i className="fas fa-angle-double-right"style={{marginLeft: "10px"}}></i></Button>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
