import AtWork from "./Components/Careers/AtWork";
import Footer from "./Components/Footer/Footer";
import WhyWe from "./Components/Careers/WhyWe";
import JobOpenings from "./Components/Careers/JobOpenings";
import NavbarSmall from "./Components/Navbar/NavbarSmall";
const Careers = () => {
    return ( 
        <div className="Products"style={{
            backgroundColor: "#171717"
        }}>
            <NavbarSmall />
            <div style={{paddingTop: "6%"}}>
            <img src="https://www.medsave.in/images/mycareer.jpg" style={{
                width: "100%",
                height: "100%"
            }} className="careerTopImg" alt=""/>
            </div>
            <h1 style={{
                fontSize: "2rem",
                color: "#1167b2",
                textAlign: "center",
                marginTop: "5%"
            }}>Let's Work Together</h1>
            <h1 style={{
                fontSize: "2.5rem",
                color: "#ccc",
                textAlign: "center",
                marginTop: "-10px"
            }}>and Explore Opportunities</h1>
            <div style={{
                textAlign: "center"
            }}>
            <div className="careerTopSec">
                <p style={{color: "#bcbcbc",lineHeight: "1.5",fontSize: "1.5rem"}}>Connection, creativity and conversation are at the heart of everything we do, which is why our growing 
                    global team spans industries and disciplines.Whether youre an aspiring chef or an architect, a web 
                    developer or an interior designer, a housekeeper or a gardener, if you care about what you do, wed 
                    love you to get in touch. Read more about our teams to find your fit below. </p>
                    <img src="http://www.ecaformacion.com/wp-content/uploads/2017/12/1.jpg" style={{
                        width: "100%",
                        height: "400px"
                    }} alt=""/>
            </div>
            </div>
            <AtWork />
            <WhyWe />
            <JobOpenings />
            <Footer />
        </div>
     );
}
 
export default Careers;