import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles((theme) => ({
  root: {
  },
  title: {
    fontSize: "3rem"
  },
  pos: {
    marginBottom: 12
  },
  cardHead: {
    fontWeight: "bold",
    fontSize: "1.5rem",
    color: "#eee"
  },
  cardText: {
    fontSize: "1rem",
    marginTop: "10px",
    lineHeight: "1.8",
    color: "#bcbcbc"
  }
}));
export default function HomesingleCard(props) {
  const classes = useStyles();
  return (
    <Card className="homeCard"style={{transition: "0.3s",backgroundColor: "#161616",boxShadow: "0px 0px 10px 0px #000"}}>
 
      <CardContent className="box">
        
      <div className="ribbon ribbon-top-right">
          <span
            style={{
              fontSize: "15px"
            }}
          >
            On Sale
          </span>
        </div>

        <Typography className={classes.title} color="" gutterBottom>
          <i
            class={props.cardIcon}
            style={{
              color: "#007FFF"
            }}
          ></i>
        </Typography>
        <Typography className={(classes.pos, classes.cardHead)}>
          {props.cardTitle}
        </Typography>
        <Typography
          className={classes.cardText}
          variant="body2"
          component="p"
          color="textSecondary"
        >
          {props.cardText}
        </Typography>
      </CardContent>
    </Card>
  );
}
