const ContactLayout = (props) => {
  
    return (
        <div className="contactcard"style={{textAlign: "center"}}>
            <h1 style={{color: "#007FFF"}}><i class={props.img}></i></h1>
            <h2 style={{color: "#eee",marginTop: "20px"}}>{props.title}</h2>
            <p style={{color: "#bcbcbc"}}>{props.text}</p>
        </div>
    );
}

export default ContactLayout;