import AboutCard from "./AboutCard";
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Jane from "../Images/About/Jane.jpg";
import Yamuna from "../Images/About/Yamuna.jpg";
import Stephan from "../Images/About/Stephan.jpeg";
import Pavithran from "../Images/About/Pavithran.jpg";
import Vicky from "../Images/About/Vicky.jpg";
import Jeeva from "../Images/About/Jeeva.jpg";
import Asif from "../Images/About/Asif.jpg";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    width: "300px"
  },
}));
       
const AboutImage = () => {

    const classes = useStyles();

    return (
        
        <div  className={classes.root}>

            <h1 style={{ textAlign: "center", background: "#121212", color: "#eee",marginBottom: "unset",paddingTop: "50px",}}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Our Team</span></h1>        

            <div className="team">


            <Grid container spacing={3}>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                    <AboutCard img={Jane} name="JANETH" role="Backend Developer" />
                </Paper>
                </Grid> 
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                    <AboutCard img={Yamuna} name="YAMUNA" role="Backend Developer" />
                </Paper>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                <AboutCard img={Stephan} name="STEPHAN" role="Graphic Designer" />
                </Paper>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                <AboutCard img={Pavithran} name="PAVITHRAN" role="Backend Developer" />
                </Paper>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                <AboutCard img={Vicky} name="VIGNESHWAR" role="Frontend Developer" />
                </Paper>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                <AboutCard img={Jeeva} name="JEEVANANDAM" role="Frontend Developer" />
                </Paper>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                <AboutCard img={Asif} name="ASIF AKRAM" role="BUSINESS Developer" />
                </Paper>
                </Grid>
                <Grid item lg={4} md={6} sm={6} xs={12}>
                <Paper className={classes.paper} style={{background: "none",boxShadow: "none"}}>
                {/* <AboutCard img={image8} name="Bess" role="Human Resource" /> */}
                </Paper>
                </Grid>
            </Grid>

                
                
                
                
                
                
                
                
            </div>

        </div>
    );
}

export default AboutImage;
