import OurProductCard from "./OurProductCard";
import ProductsList from "./ProductsList";


function createProduct (product) {
    return  <OurProductCard

    key={product.id}
    ProductImg={product.image}
    ProductTitle={product.title}
    ProductDesc={product.desc}
     /> ;
}

const OurProducts = () => {
    return ( 
        <div className="OurProductsSec">

            <h1 style={{color: "#eee",textAlign: "center"}}><span style={{borderBottom: "5px solid #007FFF",paddingBottom: "8px"}}>Our Products</span></h1>

           {ProductsList.map(createProduct)}

        </div>
     );
}
 
export default OurProducts;