import Logo from "../Navbar/Logo";
import HamBurger from "./HamBurger";
import {Link} from "react-router-dom";

const navText = {
    position: "absolute",
    top: "30px",
    background:"linear-gradient(90deg, #fc5c7d 0%,#6a82fb 100% )",
    color: "#eee",
    padding: "5px 10px",
    right: "85px",
    borderRadius: "5px"
}



const NavbarSmall = () => {
    return ( 
        <div style={{
            backgroundColor:"#171717",
            padding: "20px"
            }}>
            
            <Logo />
            <Link to = "/Careers">  <p style={navText}className="navText">We're hiring</p> </Link>
            <HamBurger />

        </div>
     );
}
 
export default NavbarSmall;