import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import FooterLink from "./FooterLink";
import FooterSocialLink from "./FooterSocialLink";
import MobileFooter from "./MobileFooter";
import {Link} from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary
  }
}));

export default function Footer() {
  const classes = useStyles();

  return (
    <div
      
      style={{
        backgroundColor: "#080808"
      }}
    >
      <div className="footer">
      <Grid container spacing={3}>
        <Grid
          item
          xs
          style={{
            paddingLeft: "50px"
          }}
        >
          <Paper
            style={{
              background: "none",
              boxShadow: "none"
            }}
            className={classes.paper}
          >
            <p
              style={{
                color: "#fff",
                fontSize: "1.2rem",
                fontWeight: "bold"
              }}
            >
              PRODUCTS
            </p>
            <FooterLink FooterLink="Digital Visiting Card" />
            <FooterLink FooterLink="Invoice Management" />
            <FooterLink FooterLink="POS" />
            <FooterLink FooterLink="CRM" />
            <FooterLink FooterLink="Reporting Software" />
            <FooterLink FooterLink="GST Billing Software" />
            <FooterLink FooterLink="Task Management" />
            <FooterLink FooterLink="Appointment" />
          </Paper>
        </Grid>

        <Grid
          item
          xs
          style={{
            paddingLeft: "50px"
          }}
        >
          <Paper
            style={{
              background: "none",
              boxShadow: "none"
            }}
            className={classes.paper}
          >
            <p
              style={{
                color: "#fff",
                fontSize: "1.2rem",
                fontWeight: "bold"
              }}
            >
              SERVICES
            </p>

            <Link to="/Services"style={{textDecoration: "none"}}>
              <FooterLink FooterLink="Web Designing & Development" />
              <FooterLink FooterLink="Software Development" />
              <FooterLink FooterLink="Graphic designing" />
              <FooterLink FooterLink="Digital Marketing" />
              <FooterLink FooterLink="E-Commerce Development" />
              <FooterLink FooterLink="Whatsapp Link Generator" />
              </Link>

          </Paper>
        </Grid>
        <Grid
          item
          xs
          style={{
            paddingLeft: "50px"
          }}
        >
          <Paper
            style={{
              background: "none",
              boxShadow: "none"
            }}
            className={classes.paper}
          >
            <p
              style={{
                color: "#fff",
                fontSize: "1.2rem",
                fontWeight: "bold"
              }}
            >
              PARTNERS
            </p>
            <FooterLink FooterLink="Reseller Program" />
            <FooterLink FooterLink="Affiliate Program" />
          </Paper>
        </Grid>
        <Grid
          item
          xs
          style={{
            paddingLeft: "50px"
          }}
        >
          <Paper
            style={{
              background: "none",
              boxShadow: "none"
            }}
            className={classes.paper}
          >
            <p
              style={{
                color: "#fff",
                fontSize: "1.2rem",
                fontWeight: "bold"
              }}
            >
              FOLLOW US ON
            </p>
            <p>
              <FooterSocialLink FooterSlink="fab fa-facebook" />
              <FooterSocialLink FooterSlink="fab fa-instagram" />
              <FooterSocialLink FooterSlink="fab fa-twitter" />
              <FooterSocialLink FooterSlink="fab fa-linkedin-in" />
              <FooterSocialLink FooterSlink="fab fa-quora" />
              <FooterSocialLink FooterSlink="fab fa-medium-m" />
              <FooterSocialLink FooterSlink="fab fa-pinterest" />
              <FooterSocialLink FooterSlink="fab fa-youtube" />
            </p>
          </Paper>
        </Grid>
      </Grid>
      <div
        style={{
          padding: "10px 0",
          backgroundColor: "#080808"
        }}
      >
        <p
          style={{
            textAlign: "center",
            color: "#fff"
          }}
        >
          &copy; All Rights Reserved | 2021
        </p>
      </div> 
      </div>
      <div className="mobile-footer">
      <MobileFooter/>
    </div>        
      
    </div>

    
  );
}
