import "../../Navbar.css";
import LogoImg from "../../Images/LogoImg.svg";
import NavImg from "../../Images/Navbar/NavImg.png";
import {Link} from "react-router-dom";
import HamBurger from "./HamBurger";

const SloganText = {
    color: "#eee",    
    marginTop: "-10px",
    fontFamily: "'Montserrat', sans-serif",
    letterSpacing: "5px"
}

const SloganTwo = {
    color: "#000",
    position: "absolute",
    bottom: "5%",
    right: "3%",
    fontSize: "1.2rem",
    letterSpacing: "3px",
    fontWeight: "bold"
}
const navText = {
    position: "absolute",
    top: "30px",
    background:"linear-gradient(90deg, #fc5c7d 0%,#6a82fb 100% )",
    color: "#eee",
    padding: "5px 10px",
    right: "85px",
    borderRadius: "5px"
}
const Navbar  = () => {
    
    return ( 
        <div>
           

            <header>
            
               <div class="header__bg"></div>

               

               <Link to="/" className="logo-link">
                    <img className="logo" src={LogoImg} />
                </Link>

              <Link to = "/Careers">  <p style={navText}className="navText">We're hiring</p> </Link>

                <img src={NavImg} className="Navimg" alt=""/>

                <HamBurger />
               
               <div className="slogan">
               
                 <h1 style={SloganText}className="SloganText1">BUILD</h1>
                 <h1 style={SloganText}className="SloganText2">BRAND</h1>
                 <h1 style={SloganText}className="SloganText3">MARKET</h1>
                 <button type="button"className="expl-btn">Explore<i className="fas fa-angle-right exp-arrow-btn"></i></button>
               </div>

               <div  style={SloganTwo}>
                   <p className="NavSloganText2">Nobody does it better than us. <span style={{color: "#007FFF"}}>#Juzlyktat</span></p>
               </div>


            </header>
        </div>
     );
}
 
export default Navbar ;