import { useState } from "react";
import {Link} from "react-router-dom";
import Logo from "./Logo";


const HamBurger = () => {

    const [navWidth, setNavWidth] = useState("0%");

    function openNav () {
        setNavWidth("100%");
    }
    function closeNav () {
        setNavWidth("0%");
    }
    

    return ( 
            <div>
                
            <span className="hamburger" style={{fontSize:"30px",cursor:"pointer",position:"absolute",right: "35px",top: "25px",color: "#007FFF"}} onClick={openNav}>&#9776;</span>

                    <div id="myNav" className="overlay"style={{width: [navWidth]}}>
                    <Logo />
                        <a href="javascript:void(0)" className="closebtn link"onClick={closeNav}>&times;</a>
                        <div class="overlay-content">
                            <Link className="link" to="/">Home</Link>
                            <Link className="link" to="/Services">Services</Link>
                            <Link className="link" to="/Products">Products</Link>
                            <Link className="link" to="/Blogs">Blogs</Link>
                            <Link className="link" to="/Careers">Careers</Link>
                            <Link className="link" to="/About">About</Link>
                            <Link className="link" to="/Contact">Contact</Link>
                        </div>
                    </div>
                </div>
     );
}
 export default HamBurger;
