

const AboutCard = (props) => {
    return (
        <div className="card">
            <img  src={props.img}></img>
            <h4 style={{color: "#eee",marginTop: "10px"}}>{props.name}</h4>
            <p style={{color: "#007FFF",fontSize: "1.2rem",fontWeight: "bold"}}>{props.role}</p>
        </div>
    );
}

export default AboutCard;