import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import JobCard from "./JobCard";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,
  },
}));

export default function JobOpenings() {
  const classes = useStyles();

  return (
    <div className= "JobCardBox" style={{
        backgroundColor: "#191a1c",
        padding: "50px 10%"
    }}>
        <h1 style={{color: "#ccc",textAlign: "center"}}><span style={{color: "#1167b2"}}>JOB</span> OPENINGS</h1>
      <Grid container spacing={3}>
        <Grid item lg={6}md={6} sm={12}xs={12}>
          <Paper className={classes.paper}style={{
              background: "none",
              boxShadow: "none"
            }}>
             <JobCard Jobtitle="Front End Developer" JobDesc1="Bachelor's degree in Computer science or equivalent practical experience."
             JobDesc2="Experience with coding in React, javaScript and Bootstrap."

             JobLangIcon3="fab fa-react"
             JobLangIcon2="fab fa-js"
             JobLangIcon1="fab fa-bootstrap"

             JobLangColor3="#00d8ff"
             JobLangColor2="#e8d44d"
             JobLangColor1="#6438b1"
             />
            </Paper>
        </Grid>
        <Grid item lg={6}md={6} sm={12}xs={12}>
          <Paper className={classes.paper}style={{
              background: "none",
              boxShadow: "none"
            }}>
               <JobCard Jobtitle="Back End Developer" JobDesc1="Bachelor's degree in Computer science or equivalent practical experience."
                JobDesc2="Experience with coding in PHP, Laravel and CodeIgnitor."
                JobLangIcon3="fab fa-php"
                JobLangIcon2="fab fa-laravel"

                JobLangColor3="#7477ae"
                JobLangColor2="#e1422d"
             />
            </Paper>
        </Grid>

        <Grid item lg={6}md={6} sm={12}xs={12}>
          <Paper className={classes.paper}style={{
              background: "none",
              boxShadow: "none"
            }}>
               <JobCard Jobtitle="Graphic Designer" JobDesc1="Familiarity with design software and technologies (such as InDesign, Illustrator, Dreamweaver, Photoshop)"
                JobDesc2="Proven graphic designing experience."
                JobLangIcon3="fas fa-paint-brush"
                JobLangIcon2="fas fa-palette"
                JobLangIcon1="fas fa-brush"

                JobLangColor3="#f67800"
                JobLangColor2="#00a5f7"
                JobLangColor1="#348f10"
             />
            </Paper>
        </Grid>

        <Grid item lg={6}md={6} sm={12}xs={12}>
          <Paper className={classes.paper}style={{
              background: "none",
              boxShadow: "none"
            }}>
               <JobCard Jobtitle="Tele Caller" JobDesc1="Good knowledge of relevant computer programs and telephone systems."
                JobDesc2="Excellent customer service skills."
                JobLangIcon3="fas fa-headset"
                JobLangIcon2="fas fa-tty"

                JobLangColor3="#85c0c8"
                JobLangColor2="#395795"
             />
            </Paper>
        </Grid>
        
      </Grid>
    </div>
  );
}
