import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';


const useStyles = makeStyles({
  root : {
    minWidth: "275"
  },
    title: {
        color: "#bcbcbc",
        fontSize: "1.6rem",
        fontWeight: "bold",
        marginTop: "10px"
    },
    pos: {
      color : "#633fd5",
      fontSize: "2rem"

    },
    content: {
      color: "#bcbcbc",
      lineHeight: "1.5",
      textAlign: "justify",
      marginTop: "10px"
    }
  });
  
  export default function ServicesCard(props) {
    const classes = useStyles();
  
    return (
      <Card className="ServicesCard"style={{
        background: "none"
        }}>
        <CardContent>            
          <Typography className={classes.pos} color="textSecondary">
          <i class={props.ServiceIcon}></i>
          </Typography>
          <Typography className={classes.title} >
            {props.ServiceTitle}
          </Typography>
          <Typography className={classes.content} >
          <i class="fas fa-pen-nib"style={{
            marginRight: "2px",
            color: "#4955bb"
          }}></i> {props.ServiceText1}
            </Typography>
            <Typography className={classes.content} >
            <i class="fas fa-pen-nib"style={{
            marginRight: "2px",
            color: "#4955bb"
          }}></i> {props.ServiceText2}
            </Typography>
        </CardContent>
      </Card>
    );
  }