import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import HomesingleCard from "./HomeSingleCard";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
    boxShadow: "none"
  }
}));

export default function HomeCard() {
  const classes = useStyles();

  return (
    <div
      style={{
        marginTop: "100px",
        padding: "50px 5%",
        backgroundColor: "#161616"
      }}
    >
      <h1
        style={{
          textAlign: "center",
          color: "#eee",
          fontSize: "16px"
        }}
      >
        WE OFFER
      </h1>
      <h1
        style={{
          textAlign: "center",
          color: "#007FFF",
          marginTop: "-5px"
        }}
        className="home-offr-text"
      >
        CLOUD BASED APPLICATION
      </h1>
      <div style={{
        textAlign: "center"
      }}>
      {/* <img className="hr-image" src="https://cloud.netlifyusercontent.com/assets/344dbf88-fdf9-42bb-adb4-46f01eedd629/f50af39a-3818-4943-b8f9-8fad15b2c22f/hr-stephan-hilbelink2.png" 
      alt="Hr Image"/> */}
      </div>
      <Grid style={{ marginTop: "30px" }} container spacing={3}>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="fas fa-copy"
              cardTitle="Invoice Management"
              cardText="Envelop better invoices with just a tap on the go. Right from dealing
              with logos, fonts, and invoice templates to promote your brand wider."
            />
          </Paper>
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="fas fa-envelope"
              cardTitle="SMS Managment"
              cardText="Send automated messages to your customers and workflows, also enable follow-ups to your SMS contacts."
            />
          </Paper>
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="fab fa-wordpress"
              cardTitle="POS - Point of Sale"
              cardText="Enabling to create & send automatic invoices via email, SMS and providing a hassle-free refund way to invoices. Having provision to the process of restocking in various locations"
            />
          </Paper>
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="fas fa-users"
              cardTitle="CRM"
              cardText="Providing solutions by size and industry  Enterprise or small business and Financial Service, Real Estate, Retail."
            />
          </Paper>
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="far fa-file-code"
              cardTitle="Reporting Software"
              cardText="You can connects with data sources, gather information and provide insights based on the input data so the user can find useful information."
            />
          </Paper>
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="far fa-money-bill-alt"
              cardTitle="Billing Software"
              cardText="you can handle time and billing tracking as well as invoicing customers for services and products."
            />
          </Paper>
        </Grid>
        <Grid item xs={12} lg={4} md={6}>
          <Paper className={classes.paper} style={{background:"none"}}>
            <HomesingleCard
              cardIcon="fas fa-tasks"
              cardTitle="Task Management"
              cardText="You can track the task and assign to various members and teams, send track progress of the project and can interchange the emails into tasks."
            />
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}
