import ServicesTopSec from "./Components/Services/ServicesTopSec";
import ServicesCardGrid from "./Components/Services/ServicesCardGrids";
import NavbarSmall from "./Components/Navbar/NavbarSmall";
import Footer from "./Components/Footer/Footer";



const Services = () => {
    return (   
        
        <div>
            <NavbarSmall />
        
        <div className="Services"style={{
            backgroundColor: "#171717",
            paddingTop: "7%"
        }}>             
            <ServicesTopSec />

            <div style={{
                 margin: "0% 10%",
                 textAlign: "center",
                 marginTop: "10%"
            }}>
                <h1 style={{
                    fontSize: "1.2rem",
                    color:"#ccc",
                    letterSpacing:"5px"
                }}>We Offer</h1>
                <h1 style={{                    
                    color:"#ccc",
                    marginTop: "-5px",
                    letterSpacing:"1px"
                }}className="servicessubText">A Wide Variety of Services</h1>
            </div>
          
            <ServicesCardGrid />
            <div className="" style={{
                background: "linear-gradient(92.88deg, rgb(69, 94, 181) 9.16%, rgb(86, 67, 204) 43.89%, rgb(103, 63, 215) 64.72%)",
                textAlign: "center",
                padding: "40px 0px"
            }}>

                <h1 style={{
                    color: "#f8f8f8"                    
                }}className="servicesBottomSec">Lets build something awesome, together.</h1>
                <button type="button"className="ServicesGIT-btn">Get In Touch</button>

            </div>
            <Footer />
        </div>
        </div>
     );
}
 
export default Services;