

export default function FooterLink(props) {
    return (      
      <a
        style={{
          display: "block",
          textDecoration: "none",
          cursor: "pointer",
          transition: "0.5s"
        }}
        className="footerlink"
      >
        <i
          class="fas fa-angle-right"
          style={{
            marginRight: "5px",
            marginBottom: "15px",
            color: "#fff"
          }}
        ></i>{" "}
        {props.FooterLink}
      </a>
    );
  }
  